<script>
  import { onMount } from "svelte";

  let src = "./images/preview_0.0.1.jpg";
  import Header2 from "./components/header.svelte";
  import Download from "./components/download.svelte";

  let randomText = "";

  const getRandomTitle = () => {
    let list = [
      "Hyper To Explore",
    ];

    randomText = list[Math.floor(Math.random() * list.length)];
  };
  onMount(() => {
    getRandomTitle();
  });
</script>

<style>
  main {
    text-align: center;
    padding: 1em;
    max-width: 240px;
    margin: 0 auto;
  }

  h1 {
    color: #ff3e00;
    text-transform: uppercase;
    font-size: 4em;
    font-weight: 100;
    cursor: default;
  }
  .preview {
    width: 600px;
    text-align: center;
    margin: 50px auto;
    transition: 750ms linear all;
  }
  .preview:hover {
    cursor: zoom-in;
    /* width: 800px; */
  }
  .preview img {
    width: 100%;
  }

  @media (min-width: 640px) {
    main {
      max-width: none;
    }
  }
</style>

<Header2 />
<main>
  <h1 on:click={getRandomTitle}>{randomText}</h1>
  <div class="preview">
    <img {src} alt="screenshot preview" />
  </div>
  <p>A modern resource manager based on Electron and Web technology ,</p>
